<script>
// import Button from '../reusable/Button.vue';
import emailjs from 'emailjs-com';
import { useToast } from "vue-toastification";
export default {
	setup() {
	},

	data() {
		return {
			serviceID: 'default_service',
			templateID: 'template_30mlgd8',
			email: "",
			name: "",
			subject: "",
			message: "",
			loading: false,

		}
	},
	// components: { Button },

	methods: {
		sendEmail() {
			this.loading = true
			const toast = useToast();

			emailjs.sendForm('service_6wvo7r8', 'template_30mlgd8', this.$refs.form, 'lOy6WnAtU4OiI33zT')
				.then((result) => {
					toast.success("Email sent successfully!")
					this.loading = false
					this.email = "",
						this.name = "",
						this.subject = "",
						this.message = "",
						console.log('SUCCESS!', result.text);
				}, (error) => {
					toast.error("An error has occurred, please try again!")
					console.log('FAILED...', error.text);
				});
		}
	}

};

</script>

<template>
	<div class="w-full md:w-1/2">
		<div
			class="leading-loose max-w-xl m-4 p-7 bg-secondary-light dark:bg-secondary-dark rounded-xl shadow-xl text-left">
			<p class="font-general-medium text-primary-dark dark:text-primary-light text-2xl mb-8">
				Contact Form
			</p>
			<form @submit.prevent="sendEmail" ref="form" class="font-general-regular space-y-7">
				<div>
					<label class="block text-lg text-primary-dark dark:text-primary-light mb-2" for="name">Full
						Name</label>
					<input
						class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
						id="name" v-model="name" name="from_name" type="text" required="" placeholder="Your Name"
						aria-label="Name" />
				</div>
				<div>
					<label class="block text-lg text-primary-dark dark:text-primary-light mb-2"
						for="email">Email</label>
					<input
						class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
						id="email" v-model="email" name="reply_to" type="email" required="" placeholder="Your Email"
						aria-label="Email" />
				</div>
				<div>
					<label class="block text-lg text-primary-dark dark:text-primary-light mb-2"
						for="subject">Subject</label>
					<input
						class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
						id="subject" v-model="subject" name="subject" type="text" required="" placeholder="Subject"
						aria-label="Subject" />
				</div>

				<div>
					<label class="block text-lg text-primary-dark dark:text-primary-light mb-2"
						for="message">Message</label>
					<textarea
						class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
						id="message" v-model="message" name="message" cols="14" rows="6"
						aria-label="Message"></textarea>
				</div>

				<div>
					<button title="Send Message" :disabled='email == "" || name == "" || subject == "" || message == ""'
						class="disabled:bg-indigo-300 px-4 py-2.5 text-white tracking-wider bg-indigo-500 hover:bg-indigo-600 focus:ring-1 focus:ring-indigo-900 rounded-lg duration-500"
						type="submit" aria-label="Send Message">

						<div class="flex flex-row items-center gap-2" v-if="loading">Sending
							<svg class="w-5 h-5 text-white animate-spin" fill="none" viewBox="0 0 24 24"
								xmlns="http://www.w3.org/2000/svg">
								<circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor"
									stroke-width="4">
								</circle>
								<path class="opacity-75"
									d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
									fill="currentColor"></path>
							</svg>
						</div>
						<span v-else>Send Message</span>

					</button>
				</div>
			</form>
		</div>
	</div>
</template>

